import { FETCH_EMPLOYEES, ADD_EMPLOYEE, EDIT_EMPLOYEE, DELETE_EMPLOYEE } from './types';
import axios from 'axios';

export const fetchEmployees = (post) => dispatch => {
        fetch('/Retrieve', {
            method: 'POST',
            headers:{
                'Content-Type': 'application/json'
            },
        body:JSON.stringify(post)
        })
        .then(res => res.json())
        .then(posts => dispatch({
            type: FETCH_EMPLOYEES,
            payload: posts
        }));
        
        // .then(data=>console.log(data));
       
}

export const addEmployee = (formData) => dispatch => {
    console.log('Action Called');
    axios.post('/notes', formData)
    .then(()=> window.location.reload())
    .then(post => dispatch({
        type: ADD_EMPLOYEE,
        payload: post
    }));

}

export const deleteEmployee = (id, formData) => dispatch => {
    fetch('/Delete/'+id,{
        method: 'DELETE',
        body: formData
      })
      .then(()=> window.location.reload())
      .then(post => dispatch({
        type: DELETE_EMPLOYEE,
        payload: post
    }));

}

export const editEmployee = (id, formEditData) => dispatch => {
    fetch('/Edit/'+id, {
        method: 'PUT',
        body:formEditData
      })
      .then(()=> window.location.reload())
      .then(post => dispatch({
        type: EDIT_EMPLOYEE,
        payload: post
    }));

}







