import React, { Component } from 'react';
import './App.css';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Apps from '@material-ui/icons/Apps';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import PersonAdd from '@material-ui/icons/PersonAdd';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import HomeIcon from '@material-ui/icons/Home';
import PowerSettingsNew from '@material-ui/icons/PowerSettingsNew';
import LibraryBooks from '@material-ui/icons/LibraryBooks';
import Smartphone from '@material-ui/icons/Smartphone';
import { BrowserRouter as Router, Redirect, Link, NavLink, Switch} from 'react-router-dom';

export default class Header extends Component {
  state = {
    top: false,
    left: false,
    bottom: false,
    right: false,
  };

  toggleDrawer = (side, open) => () => {
    this.setState({
      [side]: open,
    });
  };
  render () {

    const sideList = (
     
      <div style={{width:'250px'}}>
      <div style={{width: '100%',maxWidth: 360, backgroundColor: 'theme.palette.background.paper'}}>
      <List component="nav">

     
        <ListItem button>
          <ListItemIcon>
          <Apps style={{ margin: 'theme.spacing.unit * 2'}} color="action" />
          </ListItemIcon>
          <ListItemText inset primary='Navigation' />
        </ListItem>
        
        <Divider/>


        <Link to="/AddEmployee" style={{'textDecoration':'none'}}>
        <ListItem button>
          <ListItemIcon>
          <PersonAdd style={{ margin: 'theme.spacing.unit * 2'}} color="action" />
          </ListItemIcon>
          <ListItemText inset primary="Add New Employee" />
        </ListItem>
        </Link>
        
        <Link to="/Employees" style={{'textDecoration':'none'}}>
        <ListItem button>
          <ListItemIcon>
          <Smartphone style={{ margin: 'theme.spacing.unit * 2'}} color="action" />
          </ListItemIcon>
          <ListItemText inset primary="View Employees" />
        </ListItem>
        </Link>

        <Link to="/" style={{'textDecoration':'none'}} onClick={()=> sessionStorage.clear()}>
        <ListItem button>
          <ListItemIcon>
          <PowerSettingsNew style={{ margin: 'theme.spacing.unit * 2'}} color="action" />
          </ListItemIcon>
          <ListItemText inset primary="Logout" />
        </ListItem>
        </Link>


      </List>
    </div>
      </div>
      
      
    );

    const fullList = (
      <div style={{width:'auto'}}>
        <List></List>
        <Divider />
        <List></List>
      </div>
    );

    return (

    <div>
      <header className="header" style={{flexGrow: '1'}}>
        <AppBar position="static" style={{'backgroundColor':"#fff"}}>
          <Toolbar>
            <IconButton style={{marginLeft: '-12', marginRight:'20'}} color="inherit" aria-label="Menu">
              <MenuIcon onClick={this.toggleDrawer('left', true)} style={{color:'#606060'}}/>
            </IconButton>
           <Typography variant="title" color="inherit" style={{flex:1, color:'#606060'}} >
               Employee Directory
           </Typography>
            <Link to="/AddEmployee" style={{'textDecoration':'none'}}>
            <Button variant="outlined" color="primary" style={{border:'none',color:'#fff',
             margin: 'theme.spacing.unit', 'backgroundColor':'#00E676'}}>
            <PersonAdd />
            </Button>
            </Link>
          </Toolbar>
        </AppBar>
     
        <SwipeableDrawer
          open={this.state.left}
          onClose={this.toggleDrawer('left', false)}
          onOpen={this.toggleDrawer('left', true)}
        >
          <div
            tabIndex={0}
            role="button"
            onClick={this.toggleDrawer('left', false)}
            onKeyDown={this.toggleDrawer('left', false)}
          >
            {sideList}
          </div>
        </SwipeableDrawer>
      </header>
      </div>

);
}
}



