import React, { Component } from 'react';
import './App.css';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import { BrowserRouter as Router, Redirect, Link, NavLink, Switch} from 'react-router-dom';
import green from '@material-ui/core/colors/green';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';

export default class Register extends Component {
    constructor(){
        super();
        this.state ={
            organization:'',
            username:'',
            email:'',
            password:'',
            confpassword: '',
            buttonn: 'Submit',
            color: '#00E676'
        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    onChange(e){
        this.setState({[e.target.name]: e.target.value});
    }
    onSubmit(e){
        e.preventDefault();

        if(this.state.organization === '' || this.state.username === '' ||
            this.state.email === '' || this.state.password === '' || this.state.confpassword === ''){
                alert('Please fill all fields');
            } else if(this.state.password !== this.state.confpassword){
                alert('Passwords dont match');
            } else {
                this.setState({buttonn: 'Registering...'});
                this.setState({color: '#69F0AE'});

        const userRegister = {
            "organization":this.state.organization,
            "username": this.state.username,
            "email":this.state.email,
            "password":this.state.password,
            "confpassword":this.state.confpassword
        }
        fetch('/Register',{
            method: 'POST',
            headers:{
                'Content-Type':'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify(userRegister)
        }).then(res => res.json())
        .then(data => console.log(data))
        .then(()=>this.props.history.push("/"));
    }

    }
        state = {
          checkedB: false,
        };
      
        handleChange = name => event => {
          this.setState({ [name]: event.target.checked });
        };
  render () {
    return (
      <div style={{display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: '#00E676', width: '100%', height:'700px'}}>
          
          <Card style={{marginTop:'5%', height:520, marginLeft:'10px',marginRight:'10px'}}>
          <form style={{margin:'30px'}} onSubmit = {this.onSubmit}>
          <Typography variant="title" color="inherit" style={{flex:1, color:'#606060', marginBottom:'20px'}} >
               Employee Directory | Register
           </Typography>
           <div>
              <input placeholder='Organization Name' type="text" 
              name="organization" value={this.state.organization} onChange = {this.onChange}
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300, marginBottom: '15px',fontSize:'15px'}}/>
            </div>
            <div>
             
              <input placeholder='Username' type="text" 
               name="username" value={this.state.username} onChange = {this.onChange}
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300, marginBottom: '15px',fontSize:'15px'}}/>
            </div>
            <div>
              <input placeholder='E-mail' type="text" 
               name="email" value={this.state.email} onChange = {this.onChange}
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300, marginBottom: '15px',fontSize:'15px'}}/>
            </div>
            <div>
              <input placeholder='Password' type="password"
               name="password" value={this.state.password} onChange = {this.onChange} 
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300,marginBottom: '15px',fontSize:'15px'}}/>
            </div>
            <div>
              <input placeholder='Confirm Password' type="password" 
               name="confpassword" value={this.state.confpassword} onChange = {this.onChange}
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300,marginBottom: '15px',fontSize:'15px'}}/>
            </div>
            <div>
             <FormControlLabel
                control={
                  <Checkbox
                    checked={this.state.checkedB}
                    onChange={this.handleChange('checkedB')}
                    value="checkedB"
                    color="primary"
                    style={{color:'#00E676'}}
                  />
                }
                label="By ticking, you accept Terms and Conditions."
             />
             </div>
           
            <Button variant="outlined" color="primary" style={{color:'#fff',
              'backgroundColor':this.state.color, width:332, border:'0px'}} type="submit">
            {this.state.buttonn}
            </Button>
            <Typography variant="" color="inherit" style={{flex:1, color:'#606060', marginTop:'20px'}} >
               Already own an account ? <Link to="/" style={{textDecoration:'none', 
               color:'#00E676',marginLeft:'18%'}}
               >Login to ED
               </Link>
           </Typography>
          </form>
          </Card>
    </div>
  
    );
  }
}


