import React, { Component } from 'react';
import './App.css';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CircularProgress from '@material-ui/core/CircularProgress';
import CardMedia from '@material-ui/core/CardMedia';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import HomeIcon from '@material-ui/icons/Home';
import LibraryBooks from '@material-ui/icons/LibraryBooks';
import Smartphone from '@material-ui/icons/Smartphone';
import Business from '@material-ui/icons/Business';
import People from '@material-ui/icons/People';
import Tv from '@material-ui/icons/Tv';
import Apps from '@material-ui/icons/Apps';
import PersonAdd from '@material-ui/icons/PersonAdd';
import LocalDining from '@material-ui/icons/LocalDining';
import LocalHospital from '@material-ui/icons/LocalHospital';
import AirplanemodeActive from '@material-ui/icons/AirplanemodeActive';
import DirectionsRun from '@material-ui/icons/DirectionsRun';
import Pets from '@material-ui/icons/Pets';
import { BrowserRouter as Router, Redirect, Link, NavLink, Switch} from 'react-router-dom';
import Route from 'react-router-dom/Route';
import AddEmployee from './AddEmployee.js';
import Register from './Register.js';
import Header from './Header.js';
import ViewEmployees from './ViewEmployee.js';
import { Provider } from 'react-redux';
import store from './store';
import Delete from '@material-ui/icons/Delete';
import Work from '@material-ui/icons/Work';
import CloudUpload from '@material-ui/icons/CloudUpload';


class Main extends Component {
  render () {
    return (
      <div>
      <Route  path = "/" exact strict component={Home}/>
      <Route path="/AddEmployee" exact strict component={Header}/>
      <Route path="/Employees" exact strict component={Header}/>
      <Route  path = "/Register" exact strict component={Register}/>
        <Route path="/AddEmployee" exact strict component={AddEmployee}/>
        <Route path="/Employees" exact strict component={ViewEmployees}/>
        </div>
    );
  }
}

class Home extends Component {
  constructor(){
    super();
    this.state = {
      username: '',
      password: '',
      buttonn: 'Login',
      color: '#00E676'
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
 
  onChange(e){
    this.setState({[e.target.name]: e.target.value});
  }
  onSubmit(e){
    e.preventDefault();

    if(this.state.username === '' || this.state.password === ''){
      alert('Please fill all fields')
    }else{
        this.setState({buttonn: 'Logging In...'});
        this.setState({color: '#69F0AE'});
    const userLogin = {
      "username": this.state.username,
      "password": this.state.password  
      }

      fetch('/Login',{
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(userLogin)
      }).then(res => res.json())
      // .then(data => console.log(data));
      .then(data => sessionStorage.setItem('token', data.token))
      .then(() => this.props.history.push("/Employees")); 
    }
  }
  isAuthenticated(){
    const token = sessionStorage.getItem('token');
    return token && token.length > 10;
  }
  render () {
    const token = sessionStorage.getItem('token');
    return (
     
      <div style={{display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: '#00E676', width: '100%', height:'700px'}}>
       
           {token !== null && token.length > 10 ? <Redirect to={{pathname: "/Employees"}}/> : 
           <Card style={{marginTop:'10%', height:300, marginLeft:'10px',marginRight:'10px'}}>
          <form style={{margin:'30px'}} onSubmit={this.onSubmit}>
          <Typography variant="title" color="inherit" style={{flex:1, color:'#606060', marginBottom:'20px'}} >
               Employee Directory | Login
           </Typography>
            <div>
             
              <input placeholder='Username' type="text" 
              name="username" value = {this.state.username} onChange={this.onChange}
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300, marginBottom: '15px',fontSize:'15px'}}/>
            </div>
            <div>
             
              <input placeholder='Password' type="password" 
              name="password" value = {this.state.password} onChange={this.onChange}
              style={{padding: '13px',paddingLeft:'15px',paddingRight:'15px', borderRadius:'4px',
               border:' 1px solid #bbb', width:300,marginBottom: '15px',fontSize:'15px'}}/>
            </div>
           
            <Button variant="outlined" color="primary" 
            style={{color:'#fff',
              'backgroundColor':this.state.color, width:332, border:'0px'}} type="submit" value="Log">
           {this.state.buttonn}
            </Button>
            <Typography variant="" color="inherit" style={{flex:1, color:'#606060', marginTop:'20px'}} >
               New to Employee Directory ? <Link to="/Register" style={{textDecoration:'none', 
               color:'#00E676',marginLeft:'18%'}}
               >Register
               </Link>
           </Typography>
          </form>
          </Card>
          }
          </div>
   
  
    );
  }
}

class App extends Component {
  render() {
    return (
      <Provider store={store}>
      <Router>
      <div className="App">
        
        <Main/>
      </div>
      </Router>
      </Provider>
    );
  }
}

export default App;



